.countries{
    padding: 3rem;
    display: grid;
    gap: 2rem;
}

@media screen and (min-width:768px){
    .countries{
        grid-template-columns: auto auto;
    }
}

@media screen and (min-width:992px){
    .countries{
        grid-template-columns: auto auto auto;
    }
}
.country{
    background-color: slategrey;
    color: white;
    display: flex;
    float: left;
    justify-content: center;
    padding: 1rem;
    align-items: center;
    border-radius: 0.6rem;
    transition: all 0.3s;
    margin: 30px;
    
    width: 300px;
}
.country:hover{
    transform: scale(1.1);
}
.flag{
    width: 10rem;
    height: 7rem;
}
.btn{
    border: none;
    border-radius: 0.6rem;
    padding: 0.6rem;
    cursor: pointer;
    background-color: orange;
    transition: all 0.3s;
}

.btn:hover{
    background-color: red;
    color:white;
}
.search{
    display: flex;
    padding:20px;
    border-radius: 1rem;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    margin-top: 20px;
    width: 400px;
    text-align: center;
}